.btn {
    font-family: 'Ubuntu', sans-serif;
    position: relative;
    font-weight: 40;
    font-size: 1.1rem;
    line-height: 2;
    height: 50px;
    transition: all 200ms linear;
    border-radius: 4px;
    width: 150px;
    letter-spacing: 1px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    align-self: center;
    border: none;
    cursor: pointer;
    box-shadow: 0 12px 35px 0 rgba(19, 70, 235, 0.25);
    outline: 0;
    text-transform: capitalize;
    background-color: rgb(35, 152, 241);
}